// Typography
$sans-serif: 'Montserrat', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$sans-serif-2: 'Raleway', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
$serif: 'Palatino Linotype', 'Book Book Antiqua', 'Palatino' , serif;

// Colors
$primary-color: #09f;
$secondary-color: #ffcc2f;
$off-white-color: #f4f4f4;
$grey-color: #cdcdcd;
$heading-font-color: #333;
$paragraph-font-color: #555;
$panel-odd-color: $off-white-color;
$panel-even-color: white;
$action-color: $primary-color;


$header-font-family: $sans-serif;
$base-font-family: $sans-serif-2;

// Font Colors
$base-font-color: #555;
$base-link-color: $action-color;
$base-accent-color: $action-color;

// Font Sizes
$base-font-size: 16px;
$h1-font-size: $base-font-size * 2.25;
$h2-font-size: $base-font-size * 2;
$h3-font-size: $base-font-size * 1.75;
$h4-font-size: $base-font-size * 1.5;
$h5-font-size: $base-font-size * 1.25;
$h6-font-size: $base-font-size;

// Line height
$base-line-height: 1.6;
$header-line-height: 1.25;

// Other Sizes
$base-border-radius: 3px;
$form-border-radius: $base-border-radius;

// Vertical Spacing
$base-spacing: $base-line-height * 1em;

// Dividers
$base-border-color: $grey-color;
$form-border-color: $base-border-color;
$base-border: 1px solid $base-border-color;
$form-border: $base-border;



$button-edge: shade( $primary-color, 50%);
$wrap-padding: 14em;

$form-border-color-focus: $base-accent-color;
$form-box-shadow: inset 0 1px 3px rgba(black,0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px rgba(darken($form-border-color-focus, 5), 0.7);

$form-border-color-hover: darken($base-border-color, 15);


$small: 640px;
$medium: 769px;
$large: 1024px;

$nav-bg-color: $primary-color;
$border-radius: 3px;
$max-width: 1004px;


// Neat Overrides
// $column: 90px;
// $gutter: 30px;
// $grid-columns: 12;
// $max-width: em(1088);

// Neat Breakpoints
$medium-screen: em(640);
$large-screen: em(860);

$medium-screen-up: new-breakpoint(min-width $medium-screen 4);
$large-screen-up: new-breakpoint(min-width $large-screen 8);
